ores económicos invierten en este tipo de desarrollos para obtener publicidad y ganancias, el sector turístico uno de los más populares también cuenta con herramientas como Trivago, un buscador que le ubica al cliente información específica como el tipo de habitación, precio y confort, donde el turista tiene un gran abanico de posibilidades a escoger. A nivel académico se encuentra Google Academic, una herramienta de búsqueda para aprendices e investigadores, en las que pueden encontrar publicaciones, libros y una gran cantidad de contenido educativo para el estudio. En la aplicación de la vigilancia tecnológica se puede apreciar variedad de ayudas soportadas en aplicaciones y diversos softwares de soporte en el procesamiento de información relevante en un proceso de investigación.")]),_c('div',{staticClass:"cajon color-primario p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Actualmente el mercado ofrece diversas plataformas web que brindan soporte automatizado al proceso de vigilancia tecnológica. Estas plataformas contienen “funcionalidades de rastreo, captura y tratamiento de información; gestión de los contenidos; y administración de usuarios. De igual forma, facilitan la difusión de los resultados mediante la generación de alertas y boletines para las personas involucradas en la toma de decisiones estratégicas”. (Martínez, 2013) ")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tarjeta--container row mb-5"},[_c('div',{staticClass:"col-md tarjeta color-secundario p-5"},[_c('div',{staticClass:"row justify-content-center mb-4"},[_c('div',{staticClass:"col-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/t4/f21.png"),"alt":"imagen relacionada"}})])])]),_c('h2',{staticClass:"text-center"},[_vm._v("VIGIALE® de IALE Tecnología, S.L., España")]),_c('p',{staticClass:"text-center"},[_vm._v("Es una plataforma que integra tecnologías de la Web 2.0 para la gestión de la vigilancia tecnológica. Posibilita vigilar el entorno siguiendo notificaciones de cambio de las fuentes preestablecidas, admite la recuperación de información desde Internet de patentes, publicaciones científicas, noticias, blogs. Al automatizar la búsqueda y recopilación de información, les suministra a los usuarios la mayor información en el menor tiempo posible. Consta de las siguientes características: análisis y clasificación semántica de contenidos; filtros para la selección de información; gestión integral de estudios de vigilancia tecnológica; y búsqueda interna y clúster de resultados. ")])]),_c('div',{staticClass:"col-md tarjeta color-primario p-5"},[_c('div',{staticClass:"row justify-content-center mb-4"},[_c('div',{staticClass:"col-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/t4/f22.png"),"alt":"imagen relacionada"}})])])]),_c('h2',{staticClass:"text-center"},[_vm._v("XERKA de Aiatek/Diana Tecnología, España")]),_c('p',{staticClass:"text-center"},[_vm._v("Un software que permite la vigilancia e informa automáticamente la aparición de nueva información de interés para la toma de decisiones en la empresa. Automatiza los procesos de búsqueda, análisis, clasificación y difusión de dicha información. Combina diferentes tecnologías para automatizar las fases del proceso de vigilancia y proporciona al usuario información lista para su explotación en los procesos de inteligencia competitiva. (Martínez, 2013)")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_4_3"}},[_c('h2',[_vm._v("4.3. Plan nacional de desarrollo")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center mb-5"},[_c('div',{staticClass:"col-lg-7"},[_c('div',{staticClass:"cajon-b color-secundario p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Los planes de desarrollo son fundamentales en el crecimiento de un país en temas sociales y económicos, y se incluyen en las políticas de gobierno en un mandato, generar proyectos educativos para el mejoramiento del mismo, tienen que ir alineados con el plan de desarrollo nacional, la infraestructura y la cobertura en la educación siempre enmarcan las políticas del gobernante de turno, pero la calidad queda a un lado, la importancia de generar proyectos en base a la calidad de la educación se tendrán que tener en cuenta en las políticas públicas en los gobiernos próximos.")])]),_c('p',{staticClass:"px-4"},[_vm._v("Un plan de desarrollo se define como un instrumento legal que gestiona y promueve el desarrollo en todos sus ámbitos como el social, educativo e infraestructura, con el fin de satisfacer las necesidades insatisfechas de la población y mejorar la calidad de vida de las personas en un territorio. ")])]),_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/t4/f23.png"),"alt":"imagen relacionada"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center mb-5"},[_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/t4/f24.png"),"alt":"imagen relacionada"}})])]),_c('div',{staticClass:"col-lg-7"},[_c('div',{staticClass:"cajon color-acento contenido p-4 mb-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("El Departamento Nacional de Planeación (DNP) define el Plan Nacional de Desarrollo (PND) como “el documento que sirve de base y provee los lineamientos estratégicos de las políticas públicas formuladas por el presidente de la república a través de su equipo de gobierno. Su elaboración, socialización, evaluación y seguimiento es responsabilidad directa del DNP”.")])]),_c('div',{staticClass:"cajon color-primario p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("El Plan Nacional de Desarrollo (PND) es el instrumento formal y legal por medio del cual se trazan los objetivos del Gobierno permitiendo la subsecuente evaluación de su gestión. De acuerdo con la Constitución política de Colombia de 1991 en su artículo 339 del Título XII: \"Del Régimen Económico y de la Hacienda Pública\", Capítulo II: \"De los planes de desarrollo, el PND se compone por una parte general y un plan de inversiones de las entidades públicas del orden nacional” (DPN, 2019)")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_4_4"}},[_c('h2',[_vm._v("4.4 Innovación y creatividad educativa")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/t4/f25.svg"),"alt":"imagen relacionada"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center mb-5"},[_c('div',{staticClass:"col-lg-7"},[_c('p',[_vm._v("Podemos concluir que cuando hablamos de creatividad, generalmente pensamos en algo lejano, relacionado con personas que tienen mucho talento y genialidad, pero la creatividad está en todos nosotros, y desde esta óptica es una habilidad que desarrollan los seres humanos y no como un don otorgado a unos cuantos, por esta razón, las diferentes teorías que desde el campo de la ciencia y la psicología intentan explicar comprensivamente dónde se origina la creatividad y en qué consiste, y de esta forma estimular el pensamiento creativo para enfrentar situaciones las cuales el pensamiento lógico no brinda muchas opciones eficaces en la generación de ideas originales y en la solución de problemas.")])]),_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/t4/f26.png"),"alt":"imagen relacionada"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"contenedor bg2"},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/t4/f27.png"),"alt":"imagen relacionada"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('p',[_vm._v("El concepto de innovación, según Zaltman y otros (1973), hace referencia a tres usos relacionados entre sí. Innovación con relación a “una invención”, es decir, al proceso creativo por el cual dos o más conceptos existentes o entidades son combinados en una forma novedosa, para producir una configuración desconocida previamente. En segundo lugar, la innovación es descrita como el proceso por el cual una innovación existente llega a ser parte del estado cognitivo de un usuario y de su repertorio conductual. Y, por último, una innovación es una idea, una práctica o un artefacto material que ha sido inventado o que es contemplado como novedad, independientemente de su adopción o no adopción. De este modo, el concepto de innovación aparecerá relacionado a estos tres usos: la creación de algo desconocido, la percepción de lo creado como algo nuevo y la asimilación de ese algo como novedoso. La innovación educativa aparece mucho más ligada a los dos últimos usos, por ejemplo, el uso del trabajo grupal como estrategia de enseñanza y el uso de las nuevas tecnologías de la información y la comunicación, respectivamente.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_4_5"}},[_c('h2',[_vm._v("4.5 Desarrollo tecnológico")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-lg-7"},[_c('div',{staticClass:"p-4 fcp20 mb-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Los desarrollos tecnológicos se encuentran en todas partes del diario vivir y se mantienen en constante evolución, se combinan, se transforman ofreciendo calidad de vida y agilidad de procesos, el Internet es una de las principales herramientas tecnológicas de la actualidad que conecta personas con personas, también conecta máquinas con máquinas en lo que se denomina el internet de las cosas, pero los desarrollos también se originan en otras áreas, como la química, la salud y viajes espaciales. ")])]),_c('p',[_vm._v("La educación está siendo beneficiada por la tecnología, este proceso se viene marcando ya hace unos años permitiendo un enfoque diferente de cómo aprender, de cómo procesar la información, cómo almacenarla, las plataformas digitales y el internet han dado vida a las clases virtuales, interactuar en línea y lógicamente a distancia, un proceso que no se pensaría se fuera dar tan rápido y el usuario tendría un desenvolvimiento de fácil adaptación. El futuro es incierto y los desarrollos tecnológicos darán la pauta para innovar en la educación, y tomar estos elementos como un medio y no como un fin.")])]),_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/t4/f28.png"),"alt":"imagen relacionada"}})])])])}]

